.body{

    background: radial-gradient(#d6cdcd,#e7e7c5,#cac1c1);
}

.card:hover{

    box-shadow: 5px 10px 20px 1px rgba(0, 0, 0, 0.253)!important;
}

.card-body{
padding: 3rem 3rem !important;

}

.card-text{

    font-size: .9rem;
    padding: 0.4rem 1.9rem;
}

.container-fluid.row{
    padding-top: 6rem;
}

.card-img-top{
    transform: scale(1);
    transition: transform 0.5s ease;

}

.card-img-top:hover{
    transform: scale(1.1);

}

.overflow{
    overflow: hidden;
}